import React, { Component } from 'react';
import classes from './cart.module.css'
import axios from 'axios';


class cartComponent extends Component {

    state = {
        isLoading: true,
        data: null,
    }

    componentDidMount = () => {
        axios.get('https://5w05g4ddb1.execute-api.ap-south-1.amazonaws.com/dev/profile/listAll')
            .then(data => {
                let res = data.data.list.slice(0).sort((a, b) => {
                    var x = a.status.toLowerCase();
                    var y = b.status.toLowerCase();
                    if (x < y) {
                        return -1
                    }
                    if (x > y) {
                        return 1
                    }
                    let x1 = a.date.split('/').reverse().join('');
                    let y1 = b.date.split('/').reverse().join('');
                    return x1 > y1 ? 1 : x1 < y1 ? -1 : 0;
                    // return x < y ? -1 : x > y ? 1 : 0;
                })
                this.setState({
                    isLoading: false,
                    data: res
                })
            })
            .catch(err => {
                this.setState({
                    isLoading: false,
                })
                console.log(err);
            })
    }

    render() {

        let content = <p>'Loading ...'</p>
        if (this.state.isLoading === false) {
            if (this.state.data === null) {
                content = <p>Hope there is issue in Backend May be CORS use <a href="https://chrome.google.com/webstore/detail/moesif-orign-cors-changer/digfbfaphojjndkpccljibejjbppifbc"> Extension</a> and then Refresh </p>
            }
            if (this.state.data != null) {
                content = this.state.data.map(person => {
                    return (<div className={classes.Cart} key={person.id}>
                        <div className={classes.Img}>
                            <img src={person.img}
                                alt="img not found" />
                        </div>
                        <div className={classes.Details}>
                            <p><b>Name:</b> {person.name}</p>
                            <p><b>Gender:</b> {person.gender === 'm' ? "Male" : "Female"}</p>
                            <p><b>Date:</b> {person.date}</p>
                            <p><b>Status:</b> {person.status}</p>
                        </div>
                    </div>)
                })
            }
        }
        return (
            <div className={classes.Container}>
                <div className={classes.GridContainer}>
                    {content}
                </div>
            </div>
        );
    }
}

export default cartComponent;
